.p {
    width: 30%;
    height: 40vh;
    margin: 20px 10px;
    border: 2px solid red;
    border-radius: 10px 10px 0px 0px;
    overflow: hidden;
}

.p-browser {
    height: 20px;
    background-color: red;
    display: flex;
    align-items: center;
    position:sticky;
    z-index: 2;
}

.p-circle {
    width: 6px;
    height: 6px;
    border-radius: 50%;
    margin: 3px;
    background-color: white;
}

.click {
    background-color: red;
    position: sticky;
}

.p-img {
    width: 100%;
    height: 130%;
    transition: all 1s ease-out;
}

.p:hover .p-img {
    transform: translateY(-20%);
}

@media screen and (max-width: 480px) {
    .p {
      width: 40%;
      height: 20vh;
    }
}